import React, { useCallback, useRef, useState } from "react";
import {
  GoogleMap,
  useLoadScript,
  Marker,
  InfoWindow,
} from "@react-google-maps/api";
// import { formatRelative } from "date-fns";
import usePlacesAutocomplete, {
  getGeocode,
  getLatLng,
} from "use-places-autocomplete";
// import { NEXT_PUBLIC_GOOGLE_MAPS_API_KEY } from "../../ApiKeys";
import Food from "../../Images/Food.png";

const center = { lat: 43.6532225, lng: -79.383186 };
const libraries = ["places"];
const mapContainerStyle = {
  width: "100vw",
  height: "100vh",
};

const Search = () => {
  const {
    ready,
    value,
    suggestions: { status, data },
    setValue,
    clearSuggestions,
  } = usePlacesAutocomplete({
    requestOptions: {
      location: center,
      radius: 200 * 1000,
    },
  });
  return (
    <div
      onSelect={(address) => {
        console.log(address);
      }}
    >
      <input
        type="text"
        value={value}
        onChange={(e) => {
          setValue(e.target.value);
        }}
      />
    </div>
  );
};

const GoogleMapTest3 = () => {
  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
    //   places       
    //          
    // libraries: ["places"],
    libraries,
  });

  const [markers, setMarkers] = useState([]);
  const [selected, setSelected] = useState(null);

  // GoogleMap     
  const handleMarkers = (e) => {
    setMarkers((current) => [
      ...current,
      { lat: e.latLng.lat(), lng: e.latLng.lng(), time: new Date() },
    ]);
  };

  //  handleMarkers  onClickMap  
  // Marker state        useCallback 
  const onClickMap = useCallback(handleMarkers, []);

  //     
  const mapRef = useRef();
  const onMapLoad = useCallback((map) => {
    mapRef.current = map;
    console.log("loaded");
  }, []);

  if (loadError) return "Error loading maps";
  if (!isLoaded) return "Loading...";

  return (
    <>
      <div className="">
        <div>
          <h1>Search Bar</h1>
        </div>
        <GoogleMap
          mapContainerStyle={mapContainerStyle}
          zoom={8}
          center={center}
          // onClick={onClickMap}
          onClick={(e) => {
            console.log(e);
          }}
          onLoad={onMapLoad}
        >
          {markers.map((marker) => {
            return (
              <Marker
                key={marker.time.toISOString()}
                position={{ lat: marker.lat, lng: marker.lng }}
                icon={{
                  url: Food,
                  scaledSize: new window.google.maps.Size(30, 30),
                  // origin anchor     Marker  
                  origin: new window.google.maps.Point(0, 0),
                  anchor: new window.google.maps.Point(15, 20),
                }}
                onClick={() => {
                  setSelected(marker);
                }}
              />
            );
          })}
          {selected ? (
            // InfoWindow position  
            <InfoWindow
              position={{ lat: selected.lat, lng: selected.lng }}
              // selected null    spot   
              onCloseClick={() => {
                setSelected(null);
              }}
            >
              <div>
                <h2>Bear Spotted!</h2>
                <p>Spotted</p>
                {/* <p>Spotted {formatRelative(selected.time, new Date())}</p> */}
              </div>
            </InfoWindow>
          ) : null}
        </GoogleMap>
      </div>
    </>
  );
};

export default GoogleMapTest3;
